h1, h2, h3, h4 {
  font-family: Franklin Gothic Medium, Arial Narrow, Arial, sans-serif;
}

.header {
  color: #ccc;
  background-color: #a8323c;
  grid-template-columns: repeat(12, 1fr);
  height: 10vh;
  display: grid;
}

.header .header__titulo {
  background-color: #ff1493;
  grid-column: 2 / 9;
  height: 100%;
}

